import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from '../../shared/userService/auth/auth.guard';
import { MonitorComponent } from './monitor/monitor.component';
import { ReportComponent } from './report/report.component';
import { RoutingComponent } from './routing/routing.component';
import { ReplayComponent } from './replay/replay.component';
export const MapRoutes: Routes = [
    {
        path: '',
        children: [
            {path: '', redirectTo:'monitor', pathMatch:'full'},
            {path: 'monitor',canActivate:[AuthGuard],  component: MonitorComponent},
            {path: 'routing',canActivate:[AuthGuard],  component: RoutingComponent},
            {path: 'replay',canActivate:[AuthGuard], component: ReplayComponent},
            {path: 'report',canActivate:[AuthGuard], component: ReportComponent},
        ]
    }
];
